* {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.wrapper {
  min-width: 1540px;
  padding: 0 20px;
  margin: 20px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  z-index: 1;
}

.btn {
  width: 170px;
  padding: 10px 0;
  border-radius: 10px;
  color: white;
  border: 1px solid;
  font-weight: 700;
  cursor: pointer;
  background-color: lightseagreen;
}

.btn.red {
  background-color: red;
}

.btn.green {
  background-color: green;
}

.btn.blue {
  background-color: blue;
}

.btn.black {
  background-color: black;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}

.popup .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
}
.popup .close {
  border: none;
  cursor: pointer;
  position: absolute;
  width: 30px;
  height: 30px;
  right: -20px;
  top: -20px;
  transition: all 0.75s 0.25s;
  transform: rotate(0);
}

.popup .close:hover {
  transform: rotate(180deg) scale(1.2);
}


.popup form {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 50px;
  display: grid;
  gap: 30px;
  width: 30vw;
}

form input,
form textarea {
  padding: 16px 18px;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 10px;
}

form label {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 16px 18px;
  pointer-events: none;
  border: 1px solid transparent;
  transform-origin: 0 0;
  transition: all .3s linear;
  color: #9096A6;
  font-size: 16px;
}


form input:focus~label,
form input:not(:placeholder-shown)~label,
form textarea:focus~label,
form textarea:not(:placeholder-shown)~label {
  opacity: .65;
  font-size: 10px;
  padding: 2px 18px;
}


form textarea {
  resize: none;
}


form input::-moz-placeholder,
form textarea::-moz-placeholder {
  color: transparent;
}

form input::placeholder,
form textarea::placeholder {
  color: transparent;
}


form textarea {
  width: 100%;
  height: 150px;
}

.textarea_counter {
  position: absolute;
  right: 10px;
  bottom: 10px;
  color: #9096A6;
  font-weight: 700;
  pointer-events: none;
}

.checkbox {
  cursor: pointer;
}